import { Injectable } from '@angular/core';
import { Headers, Http, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/toPromise';
//
import { Context } from '../context';
import { CatalogInfo, CatalogItem, CatalogService } from '../entities';
//
import { EmpresaInfo } from './empresa';

//////////////////////////// MODELS /////////////////////////////////////////////////////
export class SucursalInfo extends CatalogInfo {
    empresa: EmpresaInfo;
    nombre: string;
    nombreLargo: string;
    calle: string;
    numExt: string;
    numInt: string;
    colonia: string;
    cp: string;
    idPoblacion: number;
    telefono1: string;
    telefono2: string;
    fax: string;
    paginaWeb: string;
    correo: string;

    constructor() {
        super();
        this.empresa = new EmpresaInfo();
    }
}

export class SucursalItem implements CatalogItem {
    id: number;
    codigo: string;
    nombre: string;
    activo: boolean;
    estatus?: string;
    idEmpresa: number;
}


//////////////////////////// SERVICES /////////////////////////////////////////////////////
@Injectable()
export class SucursalService extends CatalogService<SucursalInfo> {
    constructor(http: Http, context: Context) {
        super(http, context, "api/kernel/security/sucursales");
    }

    create(): Promise<SucursalInfo> {
        return Promise.resolve(new SucursalInfo());
    }

}